@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;500;800&display=swap);
html,
body {
	height: 100%;
	min-height: 100vh;
}
.App {
	min-height: 100vh;
	font-family: "Poppins", Times, serif;
}

span {
	font-family: "Poppins", Times, serif !important;
}
button {
	font-family: "Poppins", Times, serif !important;
}
label {
	font-family: "Poppins", Times, serif !important;
}
.poppins {
	font-family: "Poppins", Times, serif;
}
.rounded_lg {
	border-radius: 20px;
}

.ui.segment {
	box-shadow: none !important;
}

.bg-gradient {
	background: #47a06e;
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#47a06e", endColorstr="#3b7cb4", GradientType=1);
	-webkit-animation: gradient 15s ease infinite;
	animation: gradient 15s ease infinite;
}

.zoom_on_hover {
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
}

.zoom_on_hover:hover {
	-webkit-transform: scale(1.03);
	        transform: scale(1.03);
}

.text-iiitd {
	color: #3fada8 !important;
}

a {
	color: #3fada8 !important;
}
a:hover {
	color: #197a76 !important;
}

.top_menu > .ui.menu {
	overflow: auto;
}

.top_menu > .ui.menu::-webkit-scrollbar {
	display: none;
}

.navbar-dark {
  background-color: #3fada8;
}

.navbar-dark .nav-link {
  color: white !important;
}

.cust_link.nav-link.active {
  background-color: white;
  color: #3fada8 !important;
}

.cust_link.nav-link:hover {
  background-color: white;
  color: #3fada8 !important;
}

.notif-list {
	list-style-type: none;
	padding: 10px;
}

